.nav-open .site-wrap {
  overflow: hidden;
}
.site-wrap::before {
  background: #000;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -1;
  -webkit-transition: opacity 0.3s linear 0.1s;
  -moz-transition: opacity 0.3s linear 0.1s;
  -ms-transition: opacity 0.3s linear 0.1s;
  -o-transition: opacity 0.3s linear 0.1s;
  transition: opacity 0.3s linear 0.1s;
}
.nav-open .site-wrap::before, .search-open .site-wrap::before {
  opacity: 0.4;
  z-index: 3;
}
.nav-open .site-wrap::before .utility, .search-open .site-wrap::before .utility {
  z-index: 1;
}
.site-wrap .toggles {
  display: none;
}
@media (max-width: 991px) {
  .site-wrap .toggles {
    display: flex;
    justify-content: end;
  }
}
.site-wrap .toggles .search-toggle,
.site-wrap .nav .search-toggle {
  box-shadow: none;
  color: #165D8B;
  cursor: pointer;
  font-size: 32px;
  line-height: 1.2em;
  margin-left: 15px;
  -webkit-transition: color 0.3s linear;
  -moz-transition: color 0.3s linear;
  -ms-transition: color 0.3s linear;
  -o-transition: color 0.3s linear;
  transition: color 0.3s linear;
}
@media (max-width: 991px) {
  .site-wrap .toggles .search-toggle,
  .site-wrap .nav .search-toggle {
    font-size: 40px;
    margin-left: 0;
  }
}
.search-open .site-wrap .toggles .search-toggle .icon-search::before,
.search-open .site-wrap .nav .search-toggle .icon-search::before {
  content: "\e912";
}
.site-wrap .toggles .search-toggle:hover, .site-wrap .toggles .search-toggle:focus,
.site-wrap .nav .search-toggle:hover,
.site-wrap .nav .search-toggle:focus {
  color: #1994CE;
}
.site-wrap .toggles .nav-toggle,
.site-wrap .nav .nav-toggle {
  background: #b1d235;
  box-shadow: none;
  display: none;
  height: 40px;
  margin: 2px 0 0 15px;
  padding: 10px 5px;
  position: relative;
  width: 40px;
}
@media (max-width: 991px) {
  .site-wrap .toggles .nav-toggle,
  .site-wrap .nav .nav-toggle {
    display: block;
  }
}
.site-wrap .toggles .nav-toggle span,
.site-wrap .nav .nav-toggle span {
  background: #fff;
  border-radius: 3px;
  display: block;
  width: 30px;
  height: 2px;
  transform-origin: center;
  -webkit-transition: margin 0.3s cubic-bezier(0, 0, 0.2, 1), transform 0.3s cubic-bezier(0, 0, 0.2, 1), background 0.3s linear;
  -moz-transition: margin 0.3s cubic-bezier(0, 0, 0.2, 1), transform 0.3s cubic-bezier(0, 0, 0.2, 1), background 0.3s linear;
  -ms-transition: margin 0.3s cubic-bezier(0, 0, 0.2, 1), transform 0.3s cubic-bezier(0, 0, 0.2, 1), background 0.3s linear;
  -o-transition: margin 0.3s cubic-bezier(0, 0, 0.2, 1), transform 0.3s cubic-bezier(0, 0, 0.2, 1), background 0.3s linear;
  transition: margin 0.3s cubic-bezier(0, 0, 0.2, 1), transform 0.3s cubic-bezier(0, 0, 0.2, 1), background 0.3s linear;
}
.site-wrap .toggles .nav-toggle span + span,
.site-wrap .nav .nav-toggle span + span {
  margin-top: 7px;
}
.nav-open .site-wrap .toggles .nav-toggle span,
.nav-open .site-wrap .nav .nav-toggle span {
  position: absolute;
  left: 6px;
  top: 18px;
}
.nav-open .site-wrap .toggles .nav-toggle span:nth-of-type(1),
.nav-open .site-wrap .nav .nav-toggle span:nth-of-type(1) {
  margin-bottom: -4px;
  margin-top: 0;
  transform: rotate(45deg);
}
.nav-open .site-wrap .toggles .nav-toggle span:nth-of-type(2),
.nav-open .site-wrap .nav .nav-toggle span:nth-of-type(2) {
  margin-top: 0;
  transform: rotate(-45deg);
}
